@import '~antd/dist/antd.css';

.layout {
    padding-bottom: 4.9rem;
    box-sizing: border-box;
}

.loading {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    img {
        width: 100%;
    }

    h2 {
        text-align: center;
        color: rgb(92, 92, 92);
    }
}